h1,
h2,
h3,
h4,
h5,
h6 {
    clear: both;
    font-family: $font__sans;
}

h1 {
    font-size: 3em;
    font-family: '19thCenturyRenegade', $font__sans, sans-serif;
}

h2 {
    font-size: 2.4em;
}

h3 {
    font-size: 2.2em;
}

h4 {
    font-size: 2em;
}

h5 {
    font-size: 1.8em;
}

h6 {
    font-size: 1.6em;
}